import { createApp } from 'vue'
import App from './App.vue'

import './assets/css/peta.css'
import './assets/css/base.css'
import './assets/css/datapetalama.css'
import './assets/css/analysis.css'
import './assets/css/charts.css'
import '@fortawesome/fontawesome-free/css/all.css'
import 'leaflet'
import 'leaflet/dist/leaflet.css'
createApp(App).mount('#app')

import { library } from "@fortawesome/fontawesome-svg-core";
import { FontAwesomeIcon } from "@fortawesome/vue-fontawesome";
import { faDownload } from "@fortawesome/free-solid-svg-icons"; // Tambahkan ikon yang diperlukan

library.add(faDownload);

const app = createApp(App);
app.component("font-awesome-icon", FontAwesomeIcon); // Registrasi komponen secara global
app.mount("#app");


