<template>
  <div id="trend-analysis" class="analysis-container">
    <h4 class="analysis-title">Analisis AI Sederhana:</h4>
    <div class="stats-grid">
      <div class="stat-item">
        <span class="stat-label">Total Kasus:</span>
        <span class="stat-value">{{ totalKasus }}</span>
      </div>
      <div class="stat-item">
        <span class="stat-label">Kasus Selesai:</span>
        <span class="stat-value">{{ totalSelesai }} ({{ persentaseSelesai.toFixed(1) }}%)</span>
      </div>
    </div>
    
    <p :class="['trend-message', trendClass]">{{ trendMessage }}</p>
    
    <div class="recommendation-section">
      <h4 class="section-title">📌 Saran Kegiatan:</h4>
      <ul class="recommendation-list">
        <li v-for="(recommendation, index) in recommendations" 
            :key="'rec-'+index" 
            class="recommendation-item">
          {{ recommendation }}
        </li>
      </ul>
    </div>
    
    <div class="process-analysis">
      <h4 class="section-title">📊 Analisis Proses Kasus:</h4>
      <ul class="process-list">
        <li v-for="(count, name) in totalProses" 
            :key="'process-'+name" 
            class="process-item">
          <span class="process-name">{{ name }}:</span>
          <span class="process-count">{{ count }} kasus</span>
          <span v-if="getRecommendation(name)" class="process-recommendation">
            {{ getRecommendation(name) }}
          </span>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
import '@/assets/css/analysis.css'
export default {
  name: 'AnalysisComponent',
  props: {
    // Menerima data dari parent untuk lebih fleksibel
    kasusData: {
      type: Object,
      default: () => ({
        values: [3, 8, 3, 1, 1, 1]
      })
    },
    totalSelesai: {
      type: Number,
      default: 1
    }
  },
  data() {
    return {
      totalProses: {
        'Pembalakan Liar': 3,
        'Perambahan Hutan': 8,
        'Kejahatan TSL': 3,
        'Pencemaran Lingkungan': 1,
        'Perusakan Lingkungan': 1,
        'Kebakaran Hutan': 1
      }
    }
  },
  computed: {
    totalKasus() {
      return this.kasusData.values.reduce((a, b) => a + b, 0)
    },
    persentaseSelesai() {
      return (this.totalSelesai / this.totalKasus) * 100
    },
    trendMessage() {
      return this.persentaseSelesai > 50 
        ? "🚀 Penyelesaian kasus berjalan baik!" 
        : "⚠️ Perlu peningkatan penyelesaian kasus!"
    },
    trendClass() {
      return this.persentaseSelesai > 50 ? 'good-trend' : 'bad-trend'
    },
    recommendations() {
      let recs = []
      if (this.persentaseSelesai > 50) {
        recs.push('Fokus pada penyelesaian kasus yang masih dalam tahap awal (Pulbaket, Lidik).')
        recs.push('Optimalkan koordinasi dengan pihak terkait untuk percepatan proses hukum.')
      } else {
        recs.push('Perkuat investigasi dan pengumpulan bukti untuk mempercepat proses sidik.')
        recs.push('Tingkatkan pengawasan dan patroli di wilayah rawan pelanggaran.')
        recs.push('Tingkatkan kegiatan lapangan dengan mengumpulkan data dan informasi (Puldasi).')
      }
      
      // Rekomendasi berdasarkan analisis kasus spesifik
      if (this.totalProses['Perambahan Hutan'] > 5) {
        recs.push('Prioritaskan penanganan kasus perambahan hutan yang jumlahnya tinggi.')
      }
      if (this.totalProses['Pembalakan Liar'] > 2) {
        recs.push('Perkuat operasi gabungan untuk menangani pembalakan liar.')
      }
      
      recs.push('Evaluasi efektivitas tindakan hukum dan cari solusi yang lebih efisien.')
      return recs
    }
  },
  methods: {
    getRecommendation(processName) {
      // Rekomendasi spesifik untuk setiap jenis kasus
      const recommendations = {
        'Perambahan Hutan': "Tingkatkan patroli dan pengawasan kawasan hutan, koordinasi dengan aparat terkait",
        'Pembalakan Liar': "Perkuat operasi gabungan dan pengawasan jalur transportasi kayu",
        'Kejahatan TSL': "Tingkatkan pengawasan di kawasan konservasi dan jalur perdagangan TSL",
        'Kebakaran Hutan': "Tingkatkan sistem deteksi dini dan pemadaman kebakaran hutan"
      }
      
      const threshold = {
        'Perambahan Hutan': 5,
        'Pembalakan Liar': 2,
        'Kejahatan TSL': 2,
        'Kebakaran Hutan': 1
      }
      
      if (recommendations[processName] && this.totalProses[processName] >= (threshold[processName] || 0)) {
        return recommendations[processName]
      }
      return null
    }
  }
}
</script>