<template>
<div class="charts-container">
<div class="chart-row">
      <div class="chart-wrapper">
        <h4>📊 Progress Penyelesaian Kasus Th.2024</h4>
        <div ref="donutChart"></div>
      </div>
      
      <div class="chart-wrapper">
        <h4>📊 Progress Target Tahunan</h4>
        <div ref="progressChart"></div>
      </div>
    </div>
  
    <div class="chart-row">
      <div class="chart-wrapper">
        <h4>📊 Sebaran Kasus</h4>
        <canvas ref="barChart"></canvas>
      </div>
      
      <div class="chart-wrapper">
        <h4>📊 Progress Penyelesaian Kasus Th.2025</h4>
        <canvas ref="pieChart"></canvas>
      </div>
    </div>    
  </div>
</template>

<script>
import Chart from 'chart.js/auto'
import Plotly from 'plotly.js-dist'
import '@/assets/css/charts.css'

export default {
  name: 'ChartsComponent',
  data() {
    return {
      kasusData: {
        labels: ['Pembalakan Liar', 'Perambahan Hutan', 'Kejahatan TSL', 
                'Pencemaran Lingkungan', 'Perusakan Lingkungan', 'Kebakaran Hutan'],
        values: [3, 8, 3, 1, 1, 1]
      },
      plotlyCharts: [] // Untuk menyimpan instance Plotly chart
    }
  },
  mounted() {
    this.renderBarChart()
    this.renderPieChart()
    this.renderDonutChart()
    this.renderProgressChart()
    
    // Menambahkan event listener untuk resize
    window.addEventListener('resize', this.handleResize)
  },
  beforeUnmount() {
    // Membersihkan event listener saat komponen di-unmount
    window.removeEventListener('resize', this.handleResize)
    
    // Membersihkan Plotly charts
    this.plotlyCharts.forEach(chart => {
      Plotly.purge(chart.element)
    })
  },
  methods: {
    // Render bar chart menggunakan Chart.js
    renderBarChart() {
      const ctx = this.$refs.barChart.getContext('2d')
      new Chart(ctx, {
        type: 'bar',
        data: {
          labels: this.kasusData.labels,
          datasets: [{
            label: 'Jumlah Kasus',
            data: this.kasusData.values,
            backgroundColor: ['#FF0000', '#03d643', '#eeff05', '#0000FF', '#ff00e1', '#800000'],
            borderColor: '#000',
            borderWidth: 1
          }]
        },
        options: {
          responsive: true,
          maintainAspectRatio: false, // Tambahkan untuk kontrol aspect ratio
          plugins: {
            legend: { display: false },
            title: { 
              display: true, 
              text: 'Sebaran Kasus per Kategori',
              font: { size: 14 } // Ukuran font yang lebih kecil
            }
          },
          scales: {
            y: {
              beginAtZero: true
            }
          }
        }
      })
    },
    
    // Render pie chart menggunakan Chart.js
    renderPieChart() {
      const ctx = this.$refs.pieChart.getContext('2d')
      new Chart(ctx, {
        type: 'pie',
        data: {
          labels: ['P21', 'Puldasi', 'Sidik', 'Lidik', 'P19', 'Pulbaket', 'Operasi Pengamanan', 'laporan Informasi'],
          datasets: [{
            data: [1, 0, 6, 3, 0, 3, 2, 1],
            backgroundColor: [
              '#FF6384', '#36A2EB', '#FFCE56', '#4BC0C0', 
              '#9966FF', '#FF9F40', '#00CC66', '#FF66B2'
            ]
          }]
        },
        options: {
          responsive: true,
          maintainAspectRatio: false,
          plugins: {
            title: { 
              display: true, 
              text: 'Progress Penyelesaian Kasus',
              font: { size: 14 }
            },
            legend: {
              position: 'bottom', // Pindah legend ke bawah
              labels: {
                boxWidth: 12,
                font: { size: 10 } // Ukuran font legend lebih kecil
              }
            }
          }
        }
      })
    },
    
    // Render donut chart menggunakan Plotly
    renderDonutChart() {
      const data = [{
        values: [17, 5, 2, 2, 3],
        labels: ['P21', 'Puldasi', 'Sidik', 'P19', 'Pulbaket'],
        type: 'pie',
        hole: 0.4,
        marker: { colors: ['#FF6384', '#36A2EB', '#FFCE56', '#4BC0C0', '#9966FF'] },
        textinfo: 'label+percent',
        insidetextorientation: 'radial'
      }]

      const layout = {
        title: { text: 'Progress Penyelesaian Kasus Th.2024', font: { size: 14 } },
        height: 300,
        width: null,
        autosize: true,
        margin: { t: 50, b: 50, l: 50, r: 50 },
        showlegend: true,
        legend: { orientation: 'h', y: -0.2 } // Legend horizontal di bawah
      }

      const config = { responsive: true }
      
      const plot = Plotly.newPlot(this.$refs.donutChart, data, layout, config)
      this.plotlyCharts.push({ element: this.$refs.donutChart, plot })      
    },    
    
    // Render progress chart menggunakan Plotly
    renderProgressChart() {
      const targetTahunan = 4
      const kasusSelesai = 1
      const persentaseTarget = (kasusSelesai / targetTahunan) * 100

      const data = [{
        values: [kasusSelesai, targetTahunan - kasusSelesai],
        labels: ['Kasus Selesai', 'Sisa Target'],
        type: 'pie',
        hole: 0.4,
        marker: { colors: ['#36A2EB', '#e0e0e0'] },
        textinfo: 'none'
      }]

      const layout = {
        title: { text: 'Progress Target Tahunan', font: { size: 14 } },
        annotations: [{
          font: { size: 20 },
          showarrow: false,
          text: persentaseTarget.toFixed(1) + '%',
          x: 0.5,
          y: 0.5
        }],
        height: 300,
        width: null,
        autosize: true,
        margin: { t: 50, b: 50, l: 50, r: 50 },
        showlegend: true,
        legend: { orientation: 'h', y: -0.2 } // Legend horizontal di bawah
      }

      const config = { responsive: true }
      
      const plot = Plotly.newPlot(this.$refs.progressChart, data, layout, config)
      this.plotlyCharts.push({ element: this.$refs.progressChart, plot })
    },
    
    // Handle resize untuk Plotly charts
    handleResize() {
      this.plotlyCharts.forEach(chart => {
        Plotly.relayout(chart.element, {
          width: chart.element.clientWidth,
          height: chart.element.clientHeight
        })
      })
    }
  }
}
</script>

