          @keyup.enter="handleSearch"
          class="search-input"
        />
        <button @click="handleSearch" class="search-button">
          <i class="fas fa-search"></i> Cari
        </button>
      </div>
      <button @click="toggleCoordinateInput" class="toggle-button">
        {{ showCoordinateInput ? 'Sembunyikan' : 'Tampilkan' }} Input Koordinat
      </button>
    </div>

    <div ref="map" class="kasus-map"></div> <!--Peta -->
    

    
    <div v-if="showCoordinateInput" class="coordinate-input">
      <div class="input-group">
        <label>Latitude:</label>
        <input type="number" v-model="newLat" step="any" class="coord-input" />
      </div>
      <div class="input-group">
        <label>Longitude:</label>
        <input type="number" v-model="newLng" step="any" class="coord-input" />
      </div>
      <div class="input-group">
        <label>Warna Marker:</label>
        <input type="color" v-model="markerColor" class="color-input" />
      </div>
      <button @click="addCustomMarker" class="add-button">
        Tambah Marker
      </button>
    </div>

    <div class="analysis-panel">
      <h3><i class="fas fa-chart-pie"></i>Analisis Data Kasus</h3>
      <div class="stats">
        <div class="stat-item">
          <span class="stat-label"><i class="fas fa-map-pin"></i>Total Kasus:</span>
          <span class="stat-value">{{ analysisResult.totalTitik }}</span>
        </div>
        <div class="stat-item">
          <span class="stat-label"><i class="fas fa-tags"></i>Tipologi:</span>
          <ul>
            <li v-for="(count, tipologi) in analysisResult.tipologiCount" :key="tipologi">
              {{ tipologi }}: {{ count }}
            </li>
          </ul>
        </div>
        <div class="stat-item">
          <span class="stat-label"><i class="fas fa-tasks"></i>Proses:</span>
          <ul>
            <li v-for="(count, proses) in analysisResult.prosesCount" :key="proses">
              {{ proses }}: {{ count }}
            </li>
          </ul>
        </div>
        <div class="stat-item">
          <span class="stat-label"><i class="fas fa-map-marked-alt"></i>Wilayah:</span>
          <ul>
            <li v-for="(count, wilayah) in analysisResult.wilayahCount" :key="wilayah">
              {{ wilayah }}: {{ count }}
            </li>
          </ul>
        </div>
      </div>
    </div>    
  </div>
</template>

