          //   }
            this.map = L.map(this.$refs.map).    setView([-9.43333, 119.4957], 6.5)
        
          L.tileLayer('https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png',{
            attribution: '&copy; <a href="https://www.openstreetmap.org/copyright">OpenStreetMap</a> contributors'
            }).addTo(this.map) 
          
          // this.map.whenReady(() => {
          // console.log('Peta siap digunakan');
          // });
          //}, 
      
          // addCustomMarker() 
          // {
          //   if (!this.newLat || !this.newLng) 
          //   {
          //     alert('Harap masukkan latitude dan longitude')
          //     return;
          //   }

          //   // Format deskripsi marker (sesuai struktur loadInitialData)
          //   const description = 'Tipologi: Custom Marker|Wilayah: -|Keterangan: Titik manual|Proses: Custom';

          //   // Panggil addMarker dengan data dari form
          
      
          //   this.addMarker(
          //     parseFloat(this.newLat),
          //     parseFloat(this.newLng),
          //     this.markerColor,
          //     description
          //     // `Tipologi: Custom Marker|Wilayah: -|Keterangan: Titik tambahan manual|Proses: Custom Marker`
          //   );
      
          //   // Reset form
          //   this.newLat = null;
          //   this.newLng = null;
          //   this.showCoordinateInput = false; // Sembunyikan form (opsional)
          //   // this.runAnalysis()
          // },     
  },
        
        

    loadInitialData() 
