      this.addMarker(-8.4004, 118.0183, '#03d643', 
      `Tipologi: Perambahan Hutan|Wilayah: Dompu|Keterangan: TN. Tambora|Proses: Lidik`)

      this.addMarker(-9.9526, 124.2977,'#03d643', 
      `Tipologi: Perambahan Hutan|
