      Wilayah: Manggarai Barat|
      Keterangan: Perairan Kawasan TN.Komodo, Desa Komodo Manggarai Barat.| 
      Proses: Operasi Pengamanan`)

      // Pencemaran Lingkungan.....
      this.addMarker(-8.33960, 116.03177, '#0000FF', 
      `Tipologi: Pencemaran Lingkungan|
      Wilayah: Lombok Utara|
      Keterangan: LK.15/BPPHLHK-II/SW-3/PPNS/GKM.5.4/09/2024, PT Tiara Cipta Nirwana dan I Made Gde Putra Yasa; Dusun Gili Trawangan Desa Gili Indah Kec.Pamenang Kab.Lombok Utara Prov.NTB.| 
      Proses: Sidik`)

      // Perusakan Lingkungan.....
      this.addMarker(-8.822023, 115.987961, '#ff00e1', 
      `Tipologi: Perusakan Lingkungan|
      Wilayah: Lombok Barat|
      Keterangan: LK.16/BPPHLHK-II/SW-3/PPNS/GKM.5.4/10/2024; Desa Buwun Mas Kec. Sekotong Kab. Lombok Barat Prov.NTB.| 
      Proses: Sidik`)

      // Kebakaran Hutan......
      this.addMarker(-8.360332, 116.652471, '#800000', 
      `Tipologi: Kebakaran Hutan|
      Wilayah: Lombok Timur|
