      // Data lainnya...
      // [Tambahkan semua data marker lainnya di sini]

    }, 
    
    // // ______
    addMarker(lat, lng, color, description) 
    {
      const info = this.parseDescription(description)
      
      const popupContent = `
        <div class="marker-popup">
          <h4>${info.tipologi}</h4>
          <p><strong>Wilayah:</strong> ${info.wilayah}</p>
          <p><strong>Keterangan:</strong> ${info.keterangan}</p>
          <p><strong>Proses:</strong> ${info.proses}</p>
        </div>
      `

      const marker = L.circleMarker([lat, lng], {
        radius: 5,
        fillColor: color,
        color: '#000',
        weight: 1,
        opacity: 1,
        fillOpacity: 0.8
      }).addTo(this.map)
      
      marker.bindPopup(popupContent)
      marker.description = description.toLowerCase()
      this.markers.push(marker)
    },
        // // ______

    parseDescription(desc) 
    {
      const parts = desc.split('|')
      const result = 
      {
        tipologi: 'Tidak Diketahui',
        wilayah: 'Tidak Diketahui',
        keterangan: 'Tidak Diketahui',
        proses: 'Tidak Diketahui'
      }

      function capitalizeWords(str) 
      {
        return str.replace(/\b\w/g, char => char.toUpperCase());
      }

  
      parts.forEach(part => 
      {
        const [key, value] = part.split(':').map(s => s.trim())
        if (key && value) 
        {
          result[key.toLowerCase()] = capitalizeWords(value);
        }
      })  
  
        return result
    },

      async handleSearch() {
       
        const query = this.searchQuery.toLowerCase().trim()
        if (!query) return
        
        // Cari di marker yang ada
        const foundMarker = this.markers.find(m => 
          m.description.includes(query)
        )
      
          if (foundMarker) 
          {
            this.map.setView(foundMarker.getLatLng(), 10)
            foundMarker.openPopup()
            return
          }
      
            // Cari di OpenStreetMap Nominatim
          try 
          {
            const response = await fetch(
              `https://nominatim.openstreetmap.org/search?format=json&q=${encodeURIComponent(query)}`
            )
            const data = await response.json()
        
            if (data.length > 0) 
            {
              const { lat, lon, display_name } = data[0]
              this.map.setView([lat, lon], 13)
              L.marker([lat, lon])
                .addTo(this.map)
                .bindPopup(display_name)
                .openPopup()
            }
             else 
            {
              alert('Lokasi tidak ditemukan')
            }
          } 
            catch (error) 
            {
              console.error('Error searching location:', error)
              alert('Terjadi kesalahan saat pencarian')
            }
      },

        toggleCoordinateInput() 
        {
          this.showCoordinateInput = !this.showCoordinateInput
        },        

        runAnalysis() 
        {
          const result = 
          {
            totalTitik: this.markers.length,
            tipologiCount: {},
            wilayahCount: {},
            prosesCount: {}
          };
      
          this.markers.forEach(marker => {
            const info = this.parseDescription(marker.description)
            
            result.tipologiCount[info.tipologi] = (result.tipologiCount[info.tipologi] || 0) + 1
            result.wilayahCount[info.wilayah] = (result.wilayahCount[info.wilayah] || 0) + 1
            result.prosesCount[info.proses] = (result.prosesCount[info.proses] || 0) + 1
          })
          
          this.analysisResult = result
        },
        }
      }
     
    
  
</script>

